.blog {
	padding: 0 18%;
	box-sizing: border-box;
	height: inherit;
	width: 100%;
	align-items: center;
	margin: 4% 0;
	color: #ffa500;
	display: flex;
	justify-content: center;
	flex-flow: column;
	align-items: center;
}
.blog-stories {
	width: 100%;
	font-size: 0.8rem;
	color: #ffa500;
	font-weight: bolder;
}
.blog-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 10vh;
	width: 100%;
}
.blog-header h1 {
	font-size: 2rem;
	margin: 0;
}
.blog-posts {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: space-between;
	height: inherit;
	margin-top: 4%;
}
.blog-post {
	flex: 0 0 30%;
	height: 35vh;
	-webkit-box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	padding: 3%;
	box-sizing: border-box;
	display: grid;
	grid-template-rows: 15% 10% 60% 15%;
}
.blog-post:hover {
	transform: scale(1.09) translateY(-2%);
	-webkit-box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	box-shadow: 11px 6px 30px -11px rgba(0, 0, 0, 0.75);
	background: #304587;
	color: white;
}
.blog-post h5 {
	font-size: 0.7rem;
	margin: 0;
}
.blog-post span {
	font-size: 0.5rem;
	color: #ffa500;
}
.blog-post p {
	font-size: 0.9rem;
}
.blog-posts i {
	text-align: right;
	width: 100%;
}
.first {
	background: #304587;
	color: white;
}
.first span {
	color: white;
}

@media screen and (max-width: 768px) {
	.blog {
		padding: 0 10%;
		box-sizing: border-box;
		height: inherit;
		margin: 10% 0;
	}
	.blog-stories {
		font-size: 0.5rem;
	}
	.blog-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 10vh;
		width: 100%;
	}
	.blog-header h1 {
		font-size: 1.4rem;
	}
	.blog-posts {
		display: flex;
		flex-flow: column wrap;
		justify-content: center;
		align-items: center;
		height: inherit;
		margin-top: 4%;
	}
	.blog-post {
		width: 100%;
		height: inherit;
		padding: 10% 5%;
		margin-bottom: 5%;
	}
	.blog-post h5 {
		font-size: 0.6rem;
		margin: 0;
	}
	.blog-post span {
		font-size: 0.4rem;
		color: #ffa500;
	}
	.blog-post p {
		font-size: 0.7rem;
	}
}
