.portfolio {
	padding: 0 18%;
	box-sizing: border-box;
	height: inherit;
	width: 100%;
	align-items: center;
	margin: 2% 0;
	color: #ffa500;
	display: flex;
	justify-content: center;
	flex-flow: column;
	align-items: center;
}
.portfolio-header {
	display: flex;
	align-items: center;
	height: 10vh;
	width: 100%;
	margin-bottom: 1.5rem;
}
.portfolio-header h1 {
	font-size: 2rem;
	margin: 0;
	width: 40%;
	margin-right: auto;
}
.work-image {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: space-between;
	height: 70vh;
	margin-top: 4%;
}
.work-image .image {
	flex: 0 0 30%;
	max-height: 30vh;
	background: #e9e3e32c;
	border-radius: 0.5rem;
}
.work-image img {
	width: 100%;
	height: 100%;
}
.see-more {
	color: #ff3946;
	font-size: 0.8rem;
	width: 100%;
	text-align: center;
	font-weight: bolder;
}

@media screen and (max-width: 768px) {
	.portfolio {
		padding: 0 10%;
		box-sizing: border-box;
		height: inherit;
		margin: 10% 0;
	}
	.portfolio-header {
		display: flex;
		align-items: center;
		flex-flow: column;
		height: 10vh;
		width: 100%;
		margin-bottom: 1.5rem;
	}
	.portfolio-header h1 {
		font-size: 1.4rem;
		margin: 0;
		width: 100%;
		margin-bottom: 4%;
	}
	.work-image {
		display: flex;
		flex-flow: column wrap;
		justify-content: center;
		align-items: center;
		height: inherit;
		margin-top: 10%;
	}
	.work-image .image {
		width: 100%;
		height: 30vh;
		margin-bottom: 10%;
	}
	.work-image img {
		width: 100%;
		height: 100%;
		object-fit: fill;
	}
	.see-more {
		font-size: 0.6rem;
	}
}
