.navigation {
	display: grid;
	grid-template-columns: 15% 55% 30%;
	align-items: center;
	justify-content: center;
	width: 100%;
	z-index: 9999;
	position: sticky;
	top: 0;
	background: white;
	height: 12vh;
	font-size: 0.8rem;
	font-weight: bolder;
	color: #ffa500;
	padding: 0 6%;
	box-sizing: border-box;
}
.link {
	text-decoration: none;
	padding-right: 1.7rem;
	color: #ffa500;
}
.link:hover {
	color: #ff3946;
}
.active {
	color: #ff3946;
}
.others span {
	margin-right: 1.7rem;
}
.others button {
	border: none;
	color: #ff3946;
	padding: 0.7rem 1.2rem;
	font-size: 0.8rem;
	font-weight: bolder;
	outline: none;
	border-radius: 0.3rem;
	background: #fff;
	font-family: "DM Sans";
	cursor: pointer;
	-webkit-box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
	box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
}
.others button i {
	margin-left: 0.5rem;
}

@media screen and (max-width: 768px) {
	.navigation {
		display: grid;
		grid-template-columns: 10% 60% 30%;
		height: 10vh;
		font-size: 0.4rem;
	}
	.brand-name img {
		width: 70%;
	}
	ul {
		padding: 0;
		text-align: center;
	}
	.link {
		text-decoration: none;
		padding: 0 0.2rem;
		color: #ffa500;
	}
	.active {
		color: #ff3946;
	}
	.others {
		text-align: center;
	}
	.others span {
		margin-right: 0.2rem;
		font-size: 0.4rem;
	}
	.others button {
		border: none;
		color: #ff3946;
		padding: 0.3rem 0.3rem;
		font-size: 0.4rem;
		border-radius: 0.3rem;
		border: 1px solid #304487a2;
	}
	.others button i {
		margin-left: 0.2rem;
	}
}
