header {
	display: grid;
	grid-template-columns: 50% 50%;
	padding-left: 18%;
	padding-right: 5%;
	box-sizing: border-box;
	height: 80vh;
	width: 100%;
	align-items: center;
	margin: 2% 0;
}
.header-text {
	width: 90%;
}
.header-text h1 {
	font-size: 4rem;
	color: #ffa500;
	margin: 0;
	padding: 0;
}
.header-text p {
	color: rgba(94, 91, 91, 0.712);
	font-size: 0.7rem;
	line-height: 1.3rem;
	margin-top: 1rem;
	font-weight: bold;
}
.header-text .more {
	display: flex;
	align-items: center;
	margin-top: 5%;
}
.header-text .more .link {
	padding: 0;
}
.header-text .more button {
	background: #ff3946;
	color: white;
	font-weight: bold;
	padding: 0.7rem 1.2rem;
	font-size: 0.8rem;
	margin-right: 2rem;
	outline: none;
	border: none;
	font-family: "DM Sans";
	border-radius: 100px 100px 100px 100px;
	-webkit-box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
	box-shadow: 18px 6px 22px -11px rgba(0, 0, 0, 0.75);
	cursor: pointer;
}
.header-text .more button:hover {
	background: #da0a18de;
}
.header-text .more span {
	color: #ffa500;
	font-size: 0.8rem;
	font-weight: bold;
}
.header-image img {
	width: 100%;
	height: 100%;
}
@media screen and (max-width: 768px) {
	header {
		display: grid;
		grid-template-columns: 60% 40%;
		padding: 0 5%;
		box-sizing: border-box;
		height: 40vh;
		width: 100%;
		align-items: center;
		margin: 5% 0;
	}
	.header-text {
		width: 90%;
	}
	.header-text h1 {
		font-size: 2.3rem;
		color: #ffa500;
		margin: 0;
		padding: 0;
	}
	.header-text p {
		display: none;
	}
	.header-text .more {
		display: flex;
		align-items: center;
		align-items: center;
		margin-top: 10%;
	}
	.header-text .more button {
		background: #ff3946;
		color: white;
		font-weight: bold;
		padding: 0.7rem 1.2rem;
		font-size: 0.4rem;
		margin-right: 0.5rem;
	}
	.header-text .more span {
		color: #ffa500;
		font-size: 0.4rem;
		font-weight: bold;
	}
	.header-image img {
		width: 100%;
		height: 100%;
	}
}
